import React, { useEffect } from "react"
import Grid from "@material-ui/core/Grid"

import GeneralCard from "../components/GeneralCard"
import GpsCard from "../components/GpsCard"
import MotorControllerCard from "../components/MotorControllerCard"
import TargetVehicleCard from "../components/TargetVehicleCard"
import Networking from "../networking"

interface Props {
  networking: Networking
  setTopBarTitle: React.Dispatch<React.SetStateAction<string>>
}

export default function Page({ networking, setTopBarTitle }: Props) {
  const robotState = networking.robotState

  useEffect(() => {
    setTopBarTitle("Robot Status")
  }, [])
  return (
    <Grid container spacing={2} alignItems="flex-start">
      <Grid item>
        <GeneralCard robotState={robotState} />
      </Grid>

      <Grid item>
        <GpsCard robotState={robotState} />
      </Grid>

      <Grid item>
        <MotorControllerCard
          motorController={robotState.motorControllers.leftFront}
          id={1}
          location="left_front"
        />
      </Grid>

      <Grid item>
        <MotorControllerCard
          motorController={robotState.motorControllers.leftBack}
          id={2}
          location="left_back"
        />
      </Grid>

      <Grid item>
        <MotorControllerCard
          motorController={robotState.motorControllers.rightFront}
          id={3}
          location="right_front"
        />
      </Grid>

      <Grid item>
        <MotorControllerCard
          motorController={robotState.motorControllers.rightBack}
          id={4}
          location="right_back"
        />
      </Grid>

      <Grid item>
        <TargetVehicleCard robotState={robotState} />
      </Grid>
    </Grid>
  )
}
